import "./App.css";
import { BrowserRouter as Router, Route } from "react-router-dom";

// components
import Login from "./components/Login";
import Profile from "./components/Profile";
import ContactList from "./components/ContactList";
import Newsfeed from "./components/Newsfeed";
import Events from "./components/Events";
import Enroll from "./components/Enroll";

function App() {
  return (
    <Router basename="/ceco-vn">
      <Route basename path="/" exact component={Login} />
      <Route basename path="/my-profile" exact component={Profile} />
      <Route basename path="/contact-list" exact component={ContactList} />
      <Route basename path="/newsfeed" exact component={Newsfeed} />
      <Route basename path="/events" exact component={Events} />
      <Route basename path="/enroll" exact component={Enroll} />
    </Router>
  );
}

export default App;
